<template>
  <z-view class="principal">
    <p class="destacado">
      Desarrollo de aplicaciones
    </p>
    <div slot="extension">
      <z-spot
        v-for="(el, index) in opciones"
        :angle="-70 + (120 / opciones.length) * index"
        :key="index"
        :distance="130"
        :label="el.texto"
        class="secundario"
      >
        <i class="material-icons" style="font-size: 40px;">{{ el.icon }}</i>
      </z-spot>
    </div>
  </z-view>
</template>

<script>
const opciones = [
  { texto: "Integración de aplicaciones", icon: "call_merge" },
  { texto: "Base de datos", icon: "https" },
  { texto: "Desarrollo Web", icon: "settings" }
];

export default {
  data() {
    return {
      opciones
    };
  },
  created() {
    const animacion = this.$root.$animacion;
    animacion.pause();
  },
  destroyed() {
    const animacion = this.$root.$animacion;
    animacion.play();
  }
};
</script>

<style scoped>
.principal {
  background-color: rgba(255, 255, 255, 0.1);
  border-width: 7px;
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
  color: purple;
}
.secundario {
  color: white;
  border-width: 7px;
}
</style>
