<template>
  <div id="app">
    <z-canvas :views="$options.components" class="transparente"></z-canvas>
  </div>
</template>

<script>
import { TweenLite } from "gsap";
import { mapMutations } from "vuex";

import home from "./components/home";
import datos from "./components/vistas/datos/index";
import datos_estadisticas from "./components/vistas/datos/estadisticas";
import mobile from "./components/vistas/mobile/index";
import android_detalles from "./components/vistas/mobile/android_detalles";
import ios_detalles from "./components/vistas/mobile/ios_detalles";
import aplicaciones from "./components/vistas/aplicaciones";
import web from "./components/vistas/web";

const components = {
  home, // : () => import("./components/home")
  datos,
  datos_estadisticas,
  mobile,
  android_detalles,
  ios_detalles,
  aplicaciones,
  web
};

export default {
  components,
  methods: mapMutations(["SET_ANGULO"]),
  mounted() {
    const animacionEstado = {
      angulo: 0
    };

    this.$root.$animacion = TweenLite.to(animacionEstado, 700, {
      angulo: 365,
      repeat: -1,
      onUpdate: () => {
        this.SET_ANGULO({ angulo: animacionEstado.angulo });
      }
    });

    this.$zircle.setView("home");
  }
};
</script>

<style scoped>
.transparente {
  background-color: transparent;
}
</style>
