<template>
  <z-view size="xl" class="principal">
    <p class="titulo">Ministerio de las Mujeres, Géneros y Diversidad</p>
    <img
      slot="image"
      src="@/assets/imagen_portada_mingeneros.jpg"
      width="100%"
      height="100%"
    />

    <section slot="extension">
      <z-spot
        v-for="(item, index) in items"
        :key="index"
        :angle="anguloRelativo(index, items.length, 10)"
        :distance="160"
        :label="item.texto"
        class="secundario"
        :to-view="item.view"
      >
        <i class="material-icons" style="font-size: 40px;">{{ item.icon }}</i>

        <!-- <section slot="extension">
          <z-spot
            v-for="(subitem, subindex) in item.items"
            :angle="anguloRelativo(subindex, item.items.length, 50)"
            :distance="120"
            size="xxs"
            :key="subindex"
            style="background-color: #86007D"
          ></z-spot>
        </section>-->
      </z-spot>
    </section>
  </z-view>
</template>

<script>
import { mapState } from "vuex";

const items = [
  {
    texto: "Web",
    icon: "public",
    color: "#FFA52C",
    view: "web"
  },
  {
    texto: "Mobile",
    icon: "phone_iphone",
    color: "#FFFF41",
    view: "mobile"
  },
  {
    texto: "Aplicaciones",
    icon: "desktop_windows",
    color: "#FFA52C",
    view: "aplicaciones"
  },
  {
    texto: "Datos",
    icon: "bar_chart",
    color: "#FFFF41",
    view: "datos"
  }
];

export default {
  data() {
    return {
      items
    };
  },
  computed: {
    ...mapState(["angulo"])
  },
  methods: {
    anguloRelativo(index, count, velocidad) {
      return (360 / count) * index - this.angulo * velocidad;
    }
  }
};
</script>

<style scoped>
.principal {
  /* background-color: Green; */
  font-family: "Roboto", sans-serif;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
}

.principal .titulo {
  color: white;
}

.secundario {
  background-color: purple;
  color: white;
  /* font-size: medium;
  font-family: auto; */
}
</style>
