/* global Trianglify Particles*/

// Debounce
function debounce(func, time) {
  time = time || 100; // 100 by default if no param
  var timer;
  return function(event) {
    if (timer) clearTimeout(timer);
    timer = setTimeout(func, time, event);
  };
}

let oldChild = null;

function generaFondo() {
  var pattern = Trianglify({
    width: window.innerWidth,
    height: window.innerHeight,
    x_colors: "Spectral"
  });

  const canvas = pattern.canvas();
  canvas.style = "min-width: 100%";

  if (oldChild) document.body.removeChild(oldChild);
  document.body.appendChild(canvas);
  oldChild = canvas;
}

window.addEventListener("resize", debounce(generaFondo));
generaFondo();

window.onload = function() {
  Particles.init({
    selector: ".background",
    color: "white",
    maxParticles: 600,

    responsive: [
      {
        breakpoint: 768,
        options: {
          maxParticles: 300
          // color: "#48F2E3",
          // connectParticles: false
        }
      }
    ]
  });
};
