<template>
  <z-view slider :progress="progress" class="principal">
    <p>{{ selectedItem.text }}</p>
    <img
      v-if="selectedItem.image"
      slot="image"
      :src="selectedItem.image"
      width="100%"
      height="100%"
    />
    <div slot="extension">
      <!-- <z-spot
        v-for="(el, index) in opciones"
        :angle="(360 / opciones.length) * index"
        :key="index"
        :distance="100"
        size="s"
      >
        <i class="material-icons">
          autorenew
        </i>
      </z-spot>-->

      <z-spot @click.native="siguiente" :angle="0" :distance="100" size="m">
        <i class="material-icons md-36">autorenew</i>
      </z-spot>
    </div>
  </z-view>
</template>

<script>
const opciones = [
  // { image: require("@/assets/mobile/giphy.gif"), text: "A" },
  { image: null, text: "B" },
  { image: "", text: "C" }
];

export default {
  data() {
    return {
      itemIndex: 0,
      opciones
    };
  },
  computed: {
    progress() {
      return ((this.itemIndex + 1) / this.opciones.length) * 100;
    },
    selectedItem() {
      return this.opciones[this.itemIndex];
    }
  },
  methods: {
    siguiente() {
      this.itemIndex++;
      if (this.itemIndex >= this.opciones.length) this.itemIndex = 0;
    }
  }
};
</script>

<style></style>
