import Vue from "vue";
import zircle from "zircle";

import store from "./store";
import App from "./App.vue";
import "./background";

import "zircle/dist/zircle.css";
import "./css/background.css";
import "./css/icons.css";

Vue.use(zircle);

Vue.config.productionTip = false;

new Vue({
  store,
  render: h => h(App),
  created() {
    this.$zircle.config({
      style: {
        theme: "blue"
      }
    });
  }
}).$mount("#app");
