import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== "production";

export default new Vuex.Store({
  strict: debug,
  state: {
    angulo: 0
  },
  mutations: {
    SET_ANGULO(state, { angulo }) {
      state.angulo = angulo;
    }
  }
});
